.Calendar {
    display: grid;
    font-family: Helvetica, Arial, sans-serif;
}

@media (min-width: 600px)  {
    .Calendar {
        grid-template-columns: 50% 50%;
    }
}

@media (min-width: 900px)  {
    .Calendar {
        grid-template-columns: 33% 33% 33%;
    }
}

@media (min-width: 1200px)  {
    .Calendar {
        grid-template-columns: 25% 25% 25% 25%;
    }
}

.Calendar-Month {
    width: 20%;
}

.Calendar-MonthName {
    font-weight: 200;
    color: #ccc;
}

.Calendar-MonthTable {
    text-align: center;
}

.Calendar-Day {
    position: relative;

    padding: 8px 10px;
}

.Calendar-Day_passed {
    color: rgba(0, 0, 0, .1);
}

.Calendar-Day_otherMonth {
    visibility: hidden;
}

.Calendar-Day_current {
    outline: 2px solid rgba(255, 0, 0, 0.5);
}

.Calendar-DayInfo {
    position: absolute;
    z-index: 1;

    display: none;

    min-width: 200px;
    padding: 20px;

    background: #fff;
    box-shadow: 0 0 5px rgba(0, 0, 0, .3);

    text-align: left;
    color: #000;
}

.Calendar-DayInfoSummary {
    margin: 1.5em 0 .7em;

    font-weight: normal;
}

.Calendar-DayInfoSummary:first-child {
    margin-top: 0;
}

.Calendar-Day:hover .Calendar-DayInfo {
    display: block;
}

.Calendar-DaysOfWeek {
    color: #ccc;
}
